
import type { RouteObject } from "react-router-dom";
import NotFound from "../pages/NotFound";
import Home from "../pages/home/page";
import Courses from "../pages/courses/page";
import About from "../pages/about/page";
import Contact from "../pages/contact/page";
import Promotions from "../pages/promotions/page";
import Affiliate from "../pages/affiliate/page";
import SignIn from "../pages/auth/signin/page";
import SignUp from "../pages/auth/signup/page";
import CourseDetail from "../pages/course-detail/page";
import GetStarted from "../pages/get-started/page";
import Payment from "../pages/payment/page";

const routes: RouteObject[] = [
  {
    path: "/",
    element: <Home />,
  },
  {
    path: "/courses",
    element: <Courses />,
  },
  {
    path: "/course/:id",
    element: <CourseDetail />,
  },
  {
    path: "/payment/:courseId",
    element: <Payment />,
  },
  {
    path: "/about",
    element: <About />,
  },
  {
    path: "/contact",
    element: <Contact />,
  },
  {
    path: "/promotions",
    element: <Promotions />,
  },
  {
    path: "/affiliate",
    element: <Affiliate />,
  },
  {
    path: "/auth/signin",
    element: <SignIn />,
  },
  {
    path: "/auth/signup",
    element: <SignUp />,
  },
  {
    path: "/get-started",
    element: <GetStarted />,
  },
  {
    path: "*",
    element: <NotFound />,
  },
];

export default routes;
