
import { useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import Header from '../../components/feature/Header';
import Footer from '../../components/feature/Footer';

export default function Payment() {
  const { courseId } = useParams();
  const navigate = useNavigate();
  const [selectedPayment, setSelectedPayment] = useState('');
  const [showQR, setShowQR] = useState(false);
  const [uploadedFile, setUploadedFile] = useState<File | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Mock course data - in real app, fetch based on courseId
  const course = {
    id: 1,
    title: 'Full Stack Web Development',
    instructor: 'Sarah Johnson',
    price: 89,
    originalPrice: 129,
    image: 'https://readdy.ai/api/search-image?query=modern%20web%20development%20workspace%20with%20multiple%20monitors%20showing%20code%20editor%20and%20website%20designs%2C%20clean%20minimalist%20office%20environment%20with%20blue%20accent%20lighting%2C%20professional%20developer%20setup%20with%20laptop%20and%20coding%20books%2C%20contemporary%20tech%20atmosphere&width=300&height=200&seq=paymentcourse1&orientation=landscape'
  };

  const paymentMethods = [
    {
      id: 'kpay',
      name: 'KPay',
      logo: 'https://readdy.ai/api/search-image?query=KPay%20mobile%20payment%20app%20logo%20design%20with%20modern%20blue%20and%20white%20colors%2C%20clean%20minimalist%20fintech%20branding%2C%20professional%20payment%20service%20logo%20on%20white%20background&width=120&height=60&seq=kpay1&orientation=landscape',
      qrCode: 'https://readdy.ai/api/search-image?query=QR%20code%20for%20mobile%20payment%20scanning%2C%20black%20and%20white%20square%20barcode%20pattern%2C%20clean%20white%20background%2C%20payment%20transaction%20QR%20code%20design&width=200&height=200&seq=kpayqr1&orientation=squarish'
    },
    {
      id: 'wave',
      name: 'Wave Money',
      logo: 'https://readdy.ai/api/search-image?query=Wave%20Money%20mobile%20payment%20app%20logo%20with%20orange%20and%20blue%20colors%2C%20modern%20fintech%20branding%20design%2C%20professional%20digital%20wallet%20service%20logo%20on%20white%20background&width=120&height=60&seq=wave1&orientation=landscape',
      qrCode: 'https://readdy.ai/api/search-image?query=QR%20code%20for%20Wave%20Money%20payment%20scanning%2C%20black%20and%20white%20square%20barcode%20pattern%2C%20clean%20white%20background%2C%20mobile%20payment%20QR%20code&width=200&height=200&seq=waveqr1&orientation=squarish'
    },
    {
      id: 'aya',
      name: 'AYA Pay',
      logo: 'https://readdy.ai/api/search-image?query=AYA%20Pay%20mobile%20banking%20app%20logo%20with%20green%20and%20gold%20colors%2C%20professional%20Myanmar%20banking%20service%20branding%2C%20clean%20fintech%20logo%20design%20on%20white%20background&width=120&height=60&seq=aya1&orientation=landscape',
      qrCode: 'https://readdy.ai/api/search-image?query=QR%20code%20for%20AYA%20Pay%20banking%20transaction%2C%20black%20and%20white%20square%20barcode%20pattern%2C%20clean%20white%20background%2C%20mobile%20banking%20QR%20code%20design&width=200&height=200&seq=ayaqr1&orientation=squarish'
    }
  ];

  const handlePaymentSelect = (paymentId: string) => {
    setSelectedPayment(paymentId);
    setShowQR(false);
  };

  const handleShowQR = () => {
    if (selectedPayment) {
      setShowQR(true);
    }
  };

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setUploadedFile(file);
    }
  };

  const handleSubmitPayment = async () => {
    if (!selectedPayment || !uploadedFile) {
      alert('Please select a payment method and upload transaction screenshot');
      return;
    }

    setIsSubmitting(true);
    
    // Simulate payment processing
    setTimeout(() => {
      setIsSubmitting(false);
      alert('Payment submitted successfully! We will verify your transaction and activate your course access within 24 hours.');
      navigate('/courses');
    }, 2000);
  };

  const selectedMethod = paymentMethods.find(method => method.id === selectedPayment);

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-16">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
            Complete Your Payment
          </h1>
          <p className="text-lg text-gray-600">
            Choose your preferred payment method and complete the transaction
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Course Summary */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-xl shadow-lg p-6 sticky top-8">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Order Summary</h3>
              
              <div className="flex items-start space-x-4 mb-6">
                <img 
                  src={course.image} 
                  alt={course.title}
                  className="w-20 h-16 rounded-lg object-cover object-top"
                />
                <div className="flex-1">
                  <h4 className="font-medium text-gray-900 mb-1">{course.title}</h4>
                  <p className="text-sm text-gray-600">by {course.instructor}</p>
                </div>
              </div>
              
              <div className="border-t border-gray-200 pt-4">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-gray-600">Course Price:</span>
                  <span className="text-gray-900">${course.price}</span>
                </div>
                {course.originalPrice && (
                  <div className="flex items-center justify-between mb-2">
                    <span className="text-gray-600">Original Price:</span>
                    <span className="text-gray-500 line-through">${course.originalPrice}</span>
                  </div>
                )}
                <div className="flex items-center justify-between text-lg font-semibold text-gray-900 pt-2 border-t border-gray-200">
                  <span>Total:</span>
                  <span>${course.price}</span>
                </div>
              </div>
            </div>
          </div>

          {/* Payment Methods */}
          <div className="lg:col-span-2">
            <div className="bg-white rounded-xl shadow-lg p-8">
              <h3 className="text-xl font-semibold text-gray-900 mb-6">Select Payment Method</h3>
              
              {/* Payment Method Selection */}
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
                {paymentMethods.map((method) => (
                  <button
                    key={method.id}
                    onClick={() => handlePaymentSelect(method.id)}
                    className={`p-6 border-2 rounded-xl transition-all duration-200 cursor-pointer ${
                      selectedPayment === method.id
                        ? 'border-blue-500 bg-blue-50'
                        : 'border-gray-200 hover:border-gray-300'
                    }`}
                  >
                    <img 
                      src={method.logo} 
                      alt={method.name}
                      className="w-full h-12 object-contain object-center mb-3"
                    />
                    <p className="text-sm font-medium text-gray-900">{method.name}</p>
                  </button>
                ))}
              </div>

              {/* Show QR Button */}
              {selectedPayment && !showQR && (
                <div className="text-center mb-8">
                  <button
                    onClick={handleShowQR}
                    className="px-8 py-3 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 transition-colors whitespace-nowrap cursor-pointer"
                  >
                    Show QR Code
                  </button>
                </div>
              )}

              {/* QR Code Display */}
              {showQR && selectedMethod && (
                <div className="text-center mb-8 p-6 bg-gray-50 rounded-xl">
                  <h4 className="text-lg font-semibold text-gray-900 mb-4">
                    Scan QR Code with {selectedMethod.name}
                  </h4>
                  <div className="inline-block p-4 bg-white rounded-xl shadow-sm">
                    <img 
                      src={selectedMethod.qrCode} 
                      alt={`${selectedMethod.name} QR Code`}
                      className="w-48 h-48 object-contain object-center mx-auto"
                    />
                  </div>
                  <p className="text-sm text-gray-600 mt-4">
                    Amount: <span className="font-semibold">${course.price}</span>
                  </p>
                  <p className="text-xs text-gray-500 mt-2">
                    Open your {selectedMethod.name} app and scan this QR code to complete the payment
                  </p>
                </div>
              )}

              {/* File Upload Section */}
              {showQR && (
                <div className="mb-8">
                  <h4 className="text-lg font-semibold text-gray-900 mb-4">
                    Upload Transaction Screenshot
                  </h4>
                  <div className="border-2 border-dashed border-gray-300 rounded-xl p-8 text-center">
                    <input
                      type="file"
                      accept="image/*"
                      onChange={handleFileUpload}
                      className="hidden"
                      id="screenshot-upload"
                    />
                    <label 
                      htmlFor="screenshot-upload"
                      className="cursor-pointer"
                    >
                      <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4">
                        <i className="ri-upload-cloud-2-line text-2xl text-blue-600"></i>
                      </div>
                      <p className="text-lg font-medium text-gray-900 mb-2">
                        {uploadedFile ? uploadedFile.name : 'Upload Screenshot'}
                      </p>
                      <p className="text-sm text-gray-600">
                        Click to select your transaction screenshot
                      </p>
                    </label>
                  </div>
                  
                  {uploadedFile && (
                    <div className="mt-4 p-4 bg-green-50 border border-green-200 rounded-lg">
                      <div className="flex items-center">
                        <i className="ri-check-circle-line text-green-600 mr-2"></i>
                        <span className="text-sm text-green-800">
                          Screenshot uploaded: {uploadedFile.name}
                        </span>
                      </div>
                    </div>
                  )}
                </div>
              )}

              {/* Submit Button */}
              {showQR && uploadedFile && (
                <div className="text-center">
                  <button
                    onClick={handleSubmitPayment}
                    disabled={isSubmitting}
                    className={`px-8 py-4 font-semibold rounded-lg transition-colors whitespace-nowrap cursor-pointer ${
                      isSubmitting
                        ? 'bg-gray-400 text-white cursor-not-allowed'
                        : 'bg-green-600 text-white hover:bg-green-700'
                    }`}
                  >
                    {isSubmitting ? (
                      <span className="flex items-center">
                        <i className="ri-loader-4-line animate-spin mr-2"></i>
                        Processing...
                      </span>
                    ) : (
                      'Submit Payment'
                    )}
                  </button>
                  
                  <p className="text-sm text-gray-600 mt-4">
                    Your payment will be verified within 24 hours and course access will be activated.
                  </p>
                </div>
              )}

              {/* Instructions */}
              <div className="mt-8 p-6 bg-blue-50 rounded-xl">
                <h4 className="font-semibold text-blue-900 mb-3">Payment Instructions:</h4>
                <ol className="text-sm text-blue-800 space-y-2">
                  <li>1. Select your preferred payment method above</li>
                  <li>2. Click "Show QR Code" to display the payment QR</li>
                  <li>3. Open your mobile payment app and scan the QR code</li>
                  <li>4. Complete the payment for ${course.price}</li>
                  <li>5. Take a screenshot of the successful transaction</li>
                  <li>6. Upload the screenshot and submit for verification</li>
                </ol>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
}
